<template>
  <div class="header">

    <div v-if="logoShow">
      <router-link to="/">
        <img src="/images/svg/logo_green.svg" class="logo" alt="로고">
      </router-link>
      <img src="/images/svg/symbol.svg"
           alt="모바일햄버거버튼"
           class="hamburger"
           ref="hamburger"
           @click="toggleNav">
    </div>

    <div class="wrap" :class="{'active': isActive}">
      <img src="/images/svg/close.svg" class="close_btn" @click="closeBtn"  alt="닫기">
      <div class="category" >
        <ul class="navbar_cate">
          <li>
            <router-link to="/challenge"><a href="">CHALLENGE</a></router-link>
          </li>
          <li>
            <router-link to="/magazine"><a href="">MAGAZINE</a></router-link>
          </li>
          <li>
            <router-link to="/dailylook"><a href="">DAILYLOOK</a></router-link>
          </li>
          <li>
            <router-link to="/event"><a href="">EVENT</a></router-link>
          </li>
        </ul>
      </div>
      <div class="menu">
        <ul class="navbar_menu">
          <li @click="modalSearch" v-b-modal.modal-search>
            <img src="/images/svg/search.svg" alt="검색">
          </li>
          <li>
              <img src="/images/svg/my.svg" alt="마이페이지" @click="dropDownMyPage" />
          </li>
          <li v-if="userId">
            <img src="/images/svg/noti.svg" alt="알림" @click="dropDownNotice" />
          </li>
          <li v-if="userId">
            <img src="/images/svg/write.svg" alt="글쓰기" @click="dropDownWrite"  />
          </li>
        </ul>

        <HeaderMyPage v-if="myPageShow"/>
        <HeaderNotice v-if="noticeShow"/>
        <HeaderWrite  v-if="writeShow"/>
      </div>
    </div>

    <div class="top_btn" v-show="visible" @click="goTop">
      <img src="/images/svg/top_btn.svg" alt="위로가기버튼">
    </div>

    <ModalSearch v-model="modalSearchShow"/>
  </div>
</template>

<script>
import ModalSearch from "@/components/modal/ModalSearch";
import HeaderMyPage from "@/components/HeaderMyPage.vue"
import HeaderNotice from "@/components/HeaderNotice.vue"
import HeaderWrite from "@/components/HeaderWrite.vue"

export default {
  name: "Header",
  components: {
    HeaderMyPage,
    HeaderNotice,
    HeaderWrite,
    ModalSearch,
  },
  data() {
    return {
      isActive: false,
      path: this.$route.path,
      visible: false,
      modalSearchShow: false,
      myPageShow: false,
      noticeShow: false,
      writeShow: false,
      logoShow: true,
      userId: this.$store.getters.getUserId
    }
  },
  created() {
    window.addEventListener("scroll", this.handleScrollTop);
    window.addEventListener('resize', () => {
      if (window.innerWidth > 992) {
        document.body.style.overflow = '';
      }
    }, true);
  },
  watch: {
    'path': {
      handler(value) {
        if (value) document.body.style.overflow = '';
      },
      immediate: true // 컴포넌트가 생성되자마자 즉시 실행
    },
  },
  methods: {
    modalSearch() {
      this.modalSearchShow = !this.modalSearchShow
    },
    noticeRedirect() {
      alert('로그인이 필요한 페이지입니다.')
      if (this.path !== '/login') this.$router.push('/login')
    },
    dropDownMyPage() {
      if (this.userId) this.myPageShow = !this.myPageShow
      else {
        this.myPageShow = false
        if (this.path !== '/login') this.$router.push('/login')
      }
    },
    dropDownNotice() {
      if (this.userId) this.noticeShow = !this.noticeShow
      else {
        this.noticeShow = false
        this.noticeRedirect()
      }
    },
    dropDownWrite() {
      if (this.userId) this.writeShow = !this.writeShow
      else this.writeShow = false
    },
    handleScrollTop() {
      this.scrollY = window.scrollY;
      if (!this.visible) {
        this.visible = window.scrollY > 100;
      } else if (window.scrollY < 90) {
        this.visible = !this.visible;
      }
    },
    goTop() {
      window.scrollTo({top: 0, left: 0, behavior: 'smooth'});
    },
    toggleNav() {
      this.isActive = true
      this.logoShow = false
      if (!this.$refs.hamburger.classList.contains('not-collapsed')) {
        document.body.style.overflow = 'hidden';
      }
    },
    closeBtn() {
      this.isActive = false
      this.logoShow = true
      document.body.style.overflow = '';
    },

  }
}
</script>

